import uniPopup from '@/components/uni-popup/uni-popup.vue'
import xselect from '../../wxcomponents/xselect/xselect.vue'
export default {
	onLoad() {
		uni.request({
			url: 'https://120.24.180.246:8080/xmRepair/phoneBrand/getBrand',
			method: 'GET',
			data: {},
			success: res => {
				this.slideList = res.data.data
				console.log(this.slideList)
			}
		})
		uni.request({
			url: 'https://120.24.180.246:8080/xmRepair/phoneBrand/getBrandDetail',
			method: 'POST',
			data: {
				id:1
			},
			success: res => {
				this.phoneType = res.data.data
			}
		})
	},
	data() {
		return {
			valueId:1,
			colorId:null,
			selectedId: 0,
			popupContent: [{
					name: "银色",
					value: "#E7E3E2"
				},{
					name: "金色",
					value: "#DAC8B4"
				},{
					name: "深空灰",
					value: "#2A2526"
				}],
			isSelected: false,
			phone: '手机',
			pad: '平板',
			slideList: [
				{brand:'苹果'},
				{brand:'华为'},
				{brand:'小米'},
				{brand:'VIVO'},
				{brand:'OPPO'},
				{brand:'魅族'},
				{brand:'三星'}
			],
			phoneType: [
				{model:'iphone XS MAX'},
				{model:'iphone XS'},
				{model:'iphone XR'},
				{model:'iphone X'},
				{model:'iphone 8 Plus'},
				{model:'iphone 8'},
				{model:'iphone 7Plus'}
			]
		}
	},
	methods: {
		onNext() {
			uni.navigateTo({
				url: '../faults/faults'
			});
		},
		openPopup(e) {
			let modelSelected = this.phoneType[e].model
			console.log(e)
			uni.setStorage({
				key:'model',
				data:modelSelected
			})
			this.$refs.popup.open()
			
			//获取颜色色值
			uni.request({
				url: 'https://120.24.180.246:8080/xmRepair/phoneBrand/getBrandColour',
				method: 'POST',
				data: {
					name: modelSelected
				},
				success: res => {
					console.log(res.data.data)
					this.popupContent = res.data.data
					// uni.setStorage({
					// 	key:'color',
					// 	data:svalue
					// })
				}
			})
		},
		closePopup() {
			this.$refs.popup.close()
		},
		onSelectedId(e) {
			this.selectedId = e.currentTarget.id
			this.valueId = parseInt(e.currentTarget.id) + 1
			let svalueId = e.currentTarget.id
			let svalue = this.slideList[svalueId].brand
			uni.setStorage({
				key:'brand',
				data:svalue
			})
			uni.request({
				url: 'https://120.24.180.246:8080/xmRepair/phoneBrand/getBrandDetail',
				method: 'POST',
				data: {
					id:this.valueId
				},
				success: res => {
					this.phoneType = res.data.data
					console.log(this.phoneType)
				}
			})
		},
		onSelectedColor(e){
			this.colorId = e.currentTarget.id
			// console.log(this.colorId)
			let selectedColorName = this.popupContent[this.colorId].name
			let selectedColorValue = this.popupContent[this.colorId].value
			uni.setStorage({
				key:'colorName',
				data:selectedColorName
			})
			uni.setStorage({
				key:'colorValue',
				data:selectedColorValue
			})
		}

	},
	components: {
		uniPopup,
		xselect
	}
}
